* {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.container{
	width: 50%;
	margin: 50px auto;
	background-color: #2c3e50;
	border-radius: 9px;
}

.avatar{
	width:80px;
	height:80px;
	border-radius: 70%;
	margin: 15px 0px 0px 15px;
}

.user__info{
	display: flex;
	color: #fff;
}

.profile__info{
	display: flex;
	margin: 35px 5px 0 15px;
}
.nickname{
	padding-top: 2px; 
	padding-left: 10px;
	padding-right: 10px;
	color: #bdc3c7;
	font-size: 18px;
}

.date{
	color: #bdc3c7;
	font-size: 18px;
	padding-top: 2px; 
}

.name{
	font-weight: bold;
	font-size: 20px;
}

.name img{
	max-width: 20px;
	margin-left: 5px;
}

.arrowDown{
	display: flex;
	width: 25px;
	height: 25px;
	margin-left: 230px;
	margin-top: 40px;
}
.post_content{
	color: #fff;
	font-size: 20px;
	padding: 5px 0px 5px 5px;
	padding-left: 25px;
}
.post_image{
	width: 95%;
	padding: 0 0 15px 25px;
}
.post_image img{
	display: block;
	width: 100%;
	border-radius: 20px;
}

.allIcons{
	display: flex;
	justify-content: space-between;
	width: 65%;
	padding-bottom: 15px;
}
.allIcons img{
	width: 30px;
	height: 30px;
	cursor: pointer;
}
.comentIcon{
	display: flex;
	margin-left: 35px;
}
.comentIcon p{
	padding: 0px 0 0 3px;
}

.likeIcon{
	display: flex;
	margin-left: 35px;
}


.likeIcon p{
	padding: 3px 0 0 3px;
}


.repostIcon{
	display: flex;
	margin-left: 35px;
	margin-bottom: 15px;
}
.repostIcon p{
	padding: 4px 0 0 3px;
}

.shareIcon{
	display: flex;
	margin-left: 35px;
}
.shareIcon p{
	padding: 12px 0 0 3px;
}